<template>
  <div>
    <div class="tile is-parent">
      <article class="tile is-child box">
        <div class="columns" style="margin-bottom: 2.25rem;">
          <div class="column">
            <div class="control">
              <label class="label">From</label>
              <datepicker :config="{ enableTime: true }" v-model="dt_from"></datepicker>
            </div>
            <div class="control">
              <label class="label">Receiver id</label>
              <cleave class="input" placeholder="Enter numeral"
                      :options="{ numeral: true, numeralDecimalMark: ',', delimiter: '' }" v-model="anchor_uin"></cleave>
            </div>
            <br />
            <a class="button is-success-custom" @click="getData()">
                <span class="icon">
                  <i class="fa fa-search"></i>
                </span>
              <span>Search</span>
            </a>
            <a class="button is-success-custom" @click="csvExport()">
                <span class="icon">
                  <i class="fa fa-download"></i>
                </span>
            <span>csvExport</span>
          </a>
          </div>
          <div class="column">
            <div class="control">
              <label class="label">To</label>
              <datepicker :config="{ enableTime: true }" v-model="dt_to"></datepicker>
            </div>
            <div class="control">
              <label class="label">Room id</label>
              <cleave class="input" placeholder="Enter numeral"
                      :options="{ numeral: true, numeralDecimalMark: ',', delimiter: '' }" v-model="room_id"></cleave>
            </div>
          </div>
        </div>
        <table class="table">
          <thead>
          <tr>
            <th>
              gift_id
            </th>
            <th>
              gift_name
            </th>
            <th>
              gift_num
            </th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="sum in sums">
            <td>{{ sum.gift_id }}</td>
            <td>{{ sum.gift_name }}</td>
            <td>{{ sum.gift_num }}</td>
          </tr>
          </tbody>
        </table>
        <table class="table">
          <thead>
          <tr>
            <th v-for="column in columns" v-on:click="sortBy(column)">
              {{ column }}
            </th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="user in filteredAndSortedData">
            <td>{{ user.anchor_uin }}</td>
            <td>{{ user.gift_id }}</td>
            <td>{{ user.gift_name }}</td>
            <td>{{ user.gift_num }}</td>
            <td>{{ user.give_time }}</td>
            <td>{{ user.user_uin }}</td>
          </tr>
          </tbody>
        </table>
      </article>
    </div>
  </div>
</template>

<script>
  import Datepicker from 'vue-bulma-datepicker'
  import Cleave from 'vue-cleave'
  import $ from 'jquery'

  export default {
    components: {
      Datepicker,
      Cleave
    },
    data () {
      var dtNow = new Date()
      return {
        sortKey: 'anchor_uin',
        sortType: 'number',
        reverse: 1,
        search: '',
        columns: ['anchor_uin', 'gift_id', 'gift_name', 'gift_num', 'give_time', 'user_uin'],
        room_id: {},
        newUser: {},
        anchor_uin: {},
        users: [],
        sums: [],
        dt_from: dtNow.getFullYear() + '-' + (dtNow.getMonth() + 1) + '-' + (dtNow.getDate() - 7),
        dt_to: dtNow.getFullYear() + '-' + (dtNow.getMonth() + 1) + '-' + (dtNow.getDate() - 1)
      }
    },
    computed: {
      filteredAndSortedData () {
        // Sort the remaining values
        let ascDesc = this.reverse ? 1 : -1
        if (this.sortType === 'number') {
          return this.users.sort((a, b) => ascDesc * (a[this.sortKey] - b[this.sortKey]))
        } else {
          return this.users.sort((a, b) => ascDesc * a[this.sortKey].toString().localeCompare(b[this.sortKey].toString()))
        }
      }
    },
    methods: {
      getData: function () {
        var self = this
        $.ajax({
          context: 'application/json',
          type: 'GET',
          async: false,
          data: {
            dt_from: this.dt_from,
            room_id: this.room_id,
            anchor_uin: this.anchor_uin,
            dt_to: this.dt_to
          },
          url: 'http://monitor.talktv.vn/event/gift/'
        }).done(function (msg) {
          self.users = msg.data
          self.sums = msg.summary
        }).fail(function (jqXHR, textStatus) {
          console.log('Request failed: ' + textStatus)
        })
      },
      sortBy: function (sortKey) {
        this.reverse = (this.sortKey === sortKey) ? !this.reverse : -1
        this.sortKey = sortKey
        this.sortType = typeof (this.users[0][sortKey])
      },
      addUser: function () {
        this.users.push(this.newUser)
        this.newUser = {}
      },
      csvExport: function () {
        var csvContent = 'data:text/csv;charset=utf-8,'
        csvContent += this.users.map(function (d) {
          console.log(d)
          return JSON.stringify(d.anchor_uin + ',' + d.gift_id + ',' + d.gift_num + ',' + d.give_time + ',' + d.user_uin)
        }).join('\n').replace(/(")/mg, '')
        window.open(encodeURI(csvContent))
      }
    }
  }
</script>

<style>
  .is-success-custom{
    background-color: rgba(0, 209, 178, 0.7);
    border-color: transparent;
    color: #fff;
  }
  .button:hover, .button.is-hovered {
    background-color: whitesmoke;
    color: #00d1b2;
    border-color: rgba(181, 181, 181, 0);
  }
</style>



